import Header from './Header'
import Particles from 'react-particles-js'
import { makeStyles, Box } from '@material-ui/core'


const useStyles = makeStyles({
    particlesCanvas:{
        position: 'relative',
        opacity: '0.5',
    },
    root:{
        overflowX: 'hidden',
        overflowY: 'hidden'
    }
})

const Index = () => {
    const classes = useStyles()
    return (
        <Box component='div' className={classes.root}>
            <Header />

            <Particles canvasClassName={classes.particlesCanvas}
                params={{
                    particles: {
                        number:{
                            value:45,
                            density: {
                                enable: true,
                                value_area: 700
                            }
                        },
                        shape: {
                            type: 'circle',
                            stroke: {
                                width: 2,
                                color: 'tomato'
                            }
                        },
                        size:{
                            value: 8,
                            random: true,
                            anim:{
                                enable: true,
                                speed: 5,
                                size_min: 0.1,
                                sync: true
                            }
                        },
                        opacity:{
                            value: 1,
                            random: true,
                            anim:{
                                enable: true,
                                speed: 1,
                                opacity_min: 0.1,
                                sync: true
                            }
                        }
                    },
                    interactivity:{
                        detect_on:"canvas",
                        events:{
                          onhover:{
                            enable:true,
                            mode: "grab"
                      
                          }
                        },
                         modes:{
                          grab:{
                            distance:350,
                            duration: 0.8
                          }
                         }
                       }
                }}
            />

           
        </Box>
    )
}

export default Index
