import {useState} from 'react'
import { Box, AppBar, Toolbar, IconButton, 
    makeStyles, Typography, Avatar, Divider,
    List, ListItem, ListItemIcon, ListItemText} from '@material-ui/core'
import { Apps, ArrowBack, AssignmentInd, Home } from '@material-ui/icons'
import MobilRightMenuSlider from '@material-ui/core/Drawer'
import avatar from '../Lhay.png'
import {Link} from 'react-router-dom'

const useStyles = makeStyles((theme) => ({
    appbar : {
        background: '#222'
    },
    icons : {
        color: 'tomato'
    },
    text: {
        color: 'tan'
    },
    menuSliderContainer: {
        width: 250,
        background: '#511',
        height: '100vh'
    },
    avatar: {
        display: 'block',
        margin: '0.5rem auto',
        width: theme.spacing(13),
        height: theme.spacing(13)
    },
    listItem: {
        color: 'tan'
    }
}))

const menuItems = [
    {
        listsIcon: <Home />,
        listText: "Home",
        listPath: '/ryan-santiago'
    },
    {
        listsIcon: <AssignmentInd />,
        listText: "Resume",
        listPath: '/resume'
    },
    {
        listsIcon: <Apps />,
        listText: "Portfolio",
        listPath: '/portfolio'
    },
    {
        listsIcon: <Home />,
        listText: "Contacts",
        listPath: '/contacts'
    }
]

const Navbar = () => {
    const classes = useStyles()
    const [ state, setState] = useState({
        right: false
    })
    const toggleSlider = (slider, open) => () => {
        setState({...state, [slider]: open})
    }

    const changeTitle = () => {
        switch(window.location.pathname){
            case '/ryan-santiago/':
                return 'Home';
            case '/resume':
                return 'Resume';
            case '/portfolio':
                return 'Portfolio';
            case '/contacts':
                return 'Contacts';
            default:
                return 'Unknown Page';
        }
      }

    const sideList = slider => (
        <Box component='div' className={classes.menuSliderContainer}
        onClick={toggleSlider(slider, false)}
        >
            <Avatar src={avatar} alt='Ryan Santiago' className={classes.avatar}/>
            <Divider />
            <List>
                {menuItems.map((lsItem, key) => (
                    <ListItem button key={key} component={Link} to={lsItem.listPath}>
                        <ListItemIcon className={classes.listItem}>
                            {lsItem.listsIcon}
                        </ListItemIcon>
                        <ListItemText primary={lsItem.listText} className={classes.listItem}/>
                    </ListItem>
                ))}
                
            </List>
        </Box>
    )

    return (
        <>
        <Box component='nav'>
            <AppBar position='static' className={classes.appbar} >
                <Toolbar>
                    <IconButton onClick={toggleSlider('right', true)}>
                        <ArrowBack className={classes.icons}/>
                    </IconButton>
                    <Typography variant='h5' className={classes.text}>
                        {changeTitle()}
                    </Typography>
                    <MobilRightMenuSlider 
                    anchor='right'
                    open={state.right}
                    onClose={toggleSlider('right', false)}
                    >
                        {sideList('right')}
                    </MobilRightMenuSlider>
                </Toolbar>
            </AppBar>
        </Box>
        </>
    )
}

export default Navbar
