import { Typography, Avatar, Grid, Box, makeStyles } from '@material-ui/core'
import avatar from '../Lhay.png'
import Typed from 'react-typed'

const useStyles = makeStyles((theme) => ({
   avatar:{
       width: theme.spacing(15),
       height: theme.spacing(15),
       margin: theme.spacing(1)
   },
   title:{
       color: 'tomato'
   },
   subtitle:{
       color: 'tan'
   },
   typedContainer:{
       position: 'absolute',
       top: '50%',
       left: '50%',
       transform: 'translate(-50%, -50%)',
       width: '100vw',
       textAlign: 'center',
       zIndex: '1'
   },
   list: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden'
   }
}))


const Header = () => {
    const classes = useStyles()

    return (
        <Box className={classes.typedContainer}>
            <Grid container justify='center'>
                <Avatar src={avatar} alt='Ryan Santiago' className={classes.avatar}/>
            </Grid>
            <Typography variant='h4' className={classes.title}> 
                <Typed strings={['Ryan Santiago']}
                typeSpeed={40} />
            </Typography>
           
            <Typography variant='h5' className={classes.subtitle}> 
                <Typed strings={['Web Design', 'Web Development', 'MERN Stack']} 
                typeSpeed={40} 
                loop />
            </Typography>
            <br/>
            <Typography variant='subtitle1' className={classes.subtitle}> 
            “A user interface is like a joke. If you have to explain it, it’s not that good.”
            </Typography>
            
        </Box>
    )
}

export default Header
