import React from 'react'
import { makeStyles, Typography, Box } from '@material-ui/core'

const useStyles = makeStyles((theme) => ({
    mainContainer: {
        background: '#233'
    },
    timeLine: {
        position: 'relative',
        padding: '1rem',
        margin: '0 auto',
        "&:before": {
            content: "''",
            position: 'absolute',
            height: '100%',
            border: '1px solid tan',
            right: '40px',
            top: 0
        },
        "&:after":{
            content: "''",
            display: 'table',
            clear: 'both'
        },
        [theme.breakpoints.up('md')]:{
            padding: '2rem',
            "&:before": {
                left: 'calc(50% - 1px)',
                right: 'auto'
            }
        }
    },
    timeLineItem:{
        padding: '1rem',
        borderBottom: '2px solid tan',
        position: 'relative',
        margin: '1rem 3rem 1rem 1rem',
        clear: 'both',
        "&:after": {
            content: "''",
            position: 'absolute',
        },
        "&:before": {
            content: "''",
            position: 'absolute',
            right: '-0.625rem',
            top: 'calc(50% - 5px)',
            borderStyle: 'solid',
            borderColor: 'tomato tomato transparent transparent',
            borderWidth: '0.625rem',
            transform: 'rotate(45deg)'
        },
        [theme.breakpoints.up('md')]:{
            width: '44%',
            margin: '1rem',
            "&:nth-of-type(2n)": {
                float: 'right',
                margin: '1rem',
                borderColor: 'tan'
            },
            "&:nth-of-type(2n):before":{
                right: 'auto',
                left: '-0.625rem',
                borderColor: 'transparent transparent tomato tomato'
            }
        }
    },
    timeLineYear: {
        textAlign: 'center',
        maxWidth: '9.375rem',
        margin: '0 3rem 0 auto',
        fontSize: '1.8rem',
        background: 'tomato',
        color: 'white',
        lineHeight: 1,
        padding: '0.5rem 0 1rem',
        "&:before": {
            display: 'none'
        },
        [theme.breakpoints.up('md')]: {
            textAlign: 'center',
            margin: '0 auto',
            "&:nth-of-type(2n)": {
                float: 'none',
                margin: '0 auto'
            },
            "&:nth-of-type(2n):before":{
                display: ''
            }
        }
    },
    heading: {
        color: 'tomato',
        padding: '3rem 0',
        textTransform: 'uppercase'
    },
    subHeading: {
        color: 'white',
        padding: '0',
        textTransform: 'uppercase'
    }
}))

const Resume = () => {
    const classes = useStyles()
    return (
        <>
            <Box component='header' className={classes.mainContainer}>
                <Typography variant='h4' align='center' className={classes.heading}>
                    Work Experience
                </Typography>
                <Box component='div' className={classes.timeLine}>
                    <Typography variant='h2' className={`${classes.timeLineYear} ${classes.timeLineItem}`}> 2020 </Typography>

                    <Box component='div' className={classes.timeLineItem}>
                        <Typography variant='h5' align='center' className={classes.subHeading}>
                            Senior Software Developer
                        </Typography>
                        <Typography variant='body2' align='center' style={{color: 'tomato'}}>
                            Jeonsoft Corporation
                        </Typography>
                        <Typography variant='subtitle2' align='center' style={{color: 'tan'}}>
                            August 2020 to Present being part of the Web Development Team focusing on tools such as Javascript (ReactJS, NodeJS) and Ruby.
                        </Typography>
                    </Box>

                    <Typography variant='h2' className={`${classes.timeLineYear} ${classes.timeLineItem}`}> 2018 </Typography>

                    <Box component='div' className={classes.timeLineItem}>
                        <Typography variant='h5' align='center' className={classes.subHeading}>
                            Junior Software Developer
                        </Typography>
                        <Typography variant='body2' align='center' style={{color: 'tomato'}}>
                            Jeonsoft Corporation
                        </Typography>
                        <Typography variant='subtitle2' align='center' style={{color: 'tan'}}>
                            From August 2018 to August 2020. During this time I'm more focused on maintaining and enhancements of software using Delphi and SQL. By this time I'm fully aware how fine-tuning, optimization and refactoring really works.
                        </Typography>
                    </Box>
                </Box>

                <Typography variant='h4' align='center' className={classes.heading}>
                    Education
                </Typography>
                <Box component='div' className={classes.timeLine}>
                    <Typography variant='h2' className={`${classes.timeLineYear} ${classes.timeLineItem}`}> Tertiary </Typography>

                    <Box component='div' className={classes.timeLineItem}>
                        <Typography variant='h5' align='center' className={classes.subHeading}>
                            Bestlink College of the Philippines
                        </Typography>
                        <Typography variant='body2' align='center' style={{color: 'tomato'}}>
                            2014 - 2018
                        </Typography>
                        <Typography variant='subtitle2' align='center' style={{color: 'tan'}}>
                            Bachelor's of Science in Information Technology
                        </Typography>
                    </Box>

                    <Typography variant='h2' className={`${classes.timeLineYear} ${classes.timeLineItem}`}> Secondary </Typography>

                    <Box component='div' className={classes.timeLineItem}>
                        <Typography variant='h5' align='center' className={classes.subHeading}>
                            Caybiga High School
                        </Typography>
                        <Typography variant='body2' align='center' style={{color: 'tomato'}}>
                            2006 - 2012
                        </Typography>
                    </Box>

                    <Typography variant='h2' className={`${classes.timeLineYear} ${classes.timeLineItem}`}> Primary </Typography>

                    <Box component='div' className={classes.timeLineItem}>
                        <Typography variant='h5' align='center' className={classes.subHeading}>
                            San Rafael Elementary School
                        </Typography>
                        <Typography variant='body2' align='center' style={{color: 'tomato'}}>
                            2000 - 2006
                        </Typography>
                    </Box>
                </Box>
            </Box>
        </>
    )
}

export default Resume
